<template>
  <div class="app">
    <!-- header -->
    <AppHeader fixed>
      <b-link class="navbar-brand" to="/">
        <img class="navbar-brand-full" src="img/lg-logo.png" width="89" height="25" alt="LG Logo">
        <img class="navbar-brand-minimized" src="img/lg-logo-sm.png" width="30" height="30" alt="LG Logo">
      </b-link>
    </AppHeader>

    <!-- main -->
    <div class="app-body flex-row align-items-center pace-done footer-fixed no-sidebar">
      <b-container>
        <b-row class="justify-content-center">
          <b-col md="7" lg="5">
            <b-card
              header="Dark"
              header-bg-variant="dark"
              header-text-variant="white">
              <template v-slot:header>
                <h4 class="pt-1 pb-1 mb-0">로그인</h4>
              </template>
              <b-form ref="loginForm" @submit.prevent="handleLogin">
                <b-input-group class="mb-3">
                  <b-input-group-prepend is-text>
                    <i class="fa fa-user"></i>
                  </b-input-group-prepend>
                  <b-form-input v-model="userID"
                    type="text"
                    id="userID"
                    :placeholder="$t('login.userID')"
                    autocomplete="scms-userID"
                    autofocus
                    required
                  />
                </b-input-group>
                <b-input-group class="mb-3">
                  <b-input-group-prepend is-text>
                    <i class="fa fa-unlock-alt"></i>
                  </b-input-group-prepend>
                  <b-form-input v-model="password"
                    type="password"
                    id="userPWD"
                    :placeholder="$t('login.password')"
                    autocomplete="scms-password"
                    required
                  />
                </b-input-group>
                <b-form-checkbox class="mb-2 mr-sm-2 mb-sm-0">Remember me</b-form-checkbox>
                <hr class="my-4">
                <b-row class="pr-3 pl-3">
                  <b-col class="pr-1 pl-1">
                    <b-button block variant="secondary" @click="gotoRePW">
                      {{ $t('login.editPassword') }}
                    </b-button>
                  </b-col>
                  <b-col class="pr-1 pl-1">
                    <b-button block variant="secondary" @click="gotoRegister">
                      {{ $t('login.register') }}
                    </b-button>
                  </b-col>
                  <b-col class="pr-1 pl-1">
                    <b-button block variant="dark" type="submit">
                      {{ $t('login.login') }}
                    </b-button>
                  </b-col>
                </b-row>
              </b-form>
            </b-card>
          </b-col>
        </b-row>
      </b-container>
    </div>

    <!--footer-->
    <TheFooter>
      <div>
        <span class="ml-1">&#83;&#67;&#77;&#83;</span>
      </div>
      <div class="ml-auto">
        <span class="mr-1">Copyright &copy; 2019 LG Electronics. All Rights Reserved.</span>
      </div>
    </TheFooter>

    <!-- Modal -->
    <one-button-alert
      ref="loginFail"
    />
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import OneButtonAlert from '../components/modal/OneButtonAlert.vue'
import {
  Header as AppHeader,
  Footer as TheFooter
} from '@coreui/vue'

export default {
  name: 'Login',
  components: {
    AppHeader,
    TheFooter,
    OneButtonAlert
  },
  data: function () {
    return {
      userID: 'site_admin',
      password: 'Secudium!@#',
      isWorking: false
    }
  },
  created: function () {
  },
  methods: {
    ...mapActions('user', ['login', 'getUser']),

    handleLogin: async function () {
      if (!this.isWorking) {
        this.isWorking = true
        this.login({ username: this.userID, password: this.password })
          .then((data) => {
            this.$log.log('login.vue', data)
            this.getUser()
              .then((user) => {
                this.$log.log('login.vue', user)

                this.isWorking = false
                this.$router.replace({ path: '/' })
              })
          }).catch((err) => {
            this.$log.info('login fail', err)
            if (err.response !== undefined) {
              this.$refs.loginFail.show({
                title: '실패',
                text: '로그인 중 에러가 발생했습니다. 관리자에게 문의 주세요 [error code : ' + err.response + ']',
                confirm: false
              })
              this.isWorking = false
            } else {
              if (err === 'C012') {
                this.$refs.loginFail.show({
                  title: '실패',
                  text: '존재하지 않는 아이디입니다. 다시 확인해 주세요.',
                  confirm: false
                })
                this.$refs.loginForm.userID.focus()
              } else if (err === 'C010') {
                this.$refs.loginFail.show({
                  title: '실패',
                  text: '비밀번호가 틀렸습니다. 다시 확인해 주세요.',
                  confirm: false
                })
                this.$refs.loginForm.userPWD.focus()
              } else {
                this.$refs.loginFail.show({
                  title: '실패',
                  text: '로그인 중 에러가 발생했습니다. 관리자에게 문의 주세요 [error code : ' + err + ']',
                  confirm: false
                })
              }
              this.isWorking = false
            }
          })
      }
    },
    gotoRegister: function () {
      this.$router.push({ path: '/register' })
    },
    gotoRePW: function () {
      this.$router.push({ path: '/reissuePW' })
    }
  }
}
</script>

<style scoped>
.fa{
  width: 15px;
}
</style>
